import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'
import ReviewAlbum from '@/view/comment/comment-review-album.vue'; //点评相册

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/review-album.html',
  routes: [
    {
      path: '/',
      name: 'ReviewAlbum',
      component: ReviewAlbum
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


