// shengqu
// import PictureBar from '@/components/picture-bar.vue'; //图片条
<template>
	<div class="PictureBar">
		<ul class="ReviewAlbum_imgShow clearfix">
	 	   <li class="ReviewAlbum_img_li" @click="getIndex(i)" v-for="(imgUrl,i) in vData" :key="i">
	 	   	 <a href="javascript:;">
	 	   	 	  <img :src="imgUrl"/>
	 	   	 </a>
	 	   </li>
	 </ul>
		
	</div>
</template>

<script>
export default {
	name: 'PictureBar',
	props:['vData'],
  data () {
    return {
     
    }
  },
  methods:{
		getIndex(i){
			this.$emit('getIndex',i); //主动触发getIndex方法，'imgData'为向父组件传递的数据
		}
  },
  mounted(){
	  
  }
  

}
</script>

<style scoped lang="less">
	@m:750/375;
	
	.ReviewAlbum_img_li{
		 width: 105px*@m;
		 height: 105px*@m;
		 border-radius: 8px*@m;
		 overflow: hidden;
		 float: left;
		 margin-right: 10px*@m;
		 margin-bottom: 10px*@m;
	}
	.ReviewAlbum_img_li:nth-child(3n+3){
		margin-right: 0;
	}
	.ReviewAlbum_img_li img{
		 width: 100%;
		 height: 100%;
		 object-fit:cover;
	}
</style>


