<!--// import ReviewAlbum from '@/view/review-album.vue'; //点评相册-->
// end完成
<template>
<div class="ReviewAlbum" style="padding: 0 40px;">
	 <!-- <div class="ReviewAlbum_top">
	 	  <a href="javascript:;"><img src="@/assets/comment-bar/in_backspace_icon@2x.png" class="ReviewAlbum_img"/></a>
	 	  <h3 class="ReviewAlbum_title">iPhone X 的图片</h3>
	 </div>
	 <ul class="ReviewAlbum_tab">
	 	  <router-link to="all-picture" tag="li" active-class="tabActive">图片</router-link>
	 	  <router-link to="friend-picture" tag="li" active-class="tabActive">网友上传</router-link>
	 </ul> -->
	 <div class="ReviewAlbum_imgShow clearfix">
	 	   <PictureBar :vData="data.imgs_url" @getIndex="getImgIndex"/>
	 </div>
	 <!-- <div class="ReviewAlbum_imgShow clearfix" v-else-if="this.$route.params.type=='friend-picture'">
	 	   <PictureBar/>
	 </div> -->
</div>
</template>
<style scoped lang="less">
	@m:750/375;
	.ReviewAlbum_top{
		width: 100%;height: 28px*@m;
		text-align: center;
		line-height: 28px*@m;
		position: relative;
	}
	.ReviewAlbum_title{
		font-family: PingFangSC-Semibold;
		font-size: 28px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 28px*@m;
	}
	.ReviewAlbum_img{
		display: block;
		width: 27.6px*@m;
		height: 19.3px*@m;
		position: absolute;
		left: 0;
		top: 4px*@m;
	}
	.ReviewAlbum_tab{
		width: 100%;height: 18px*@m;
		margin-top: 37px*@m;
		text-align: center;
		margin-bottom: 30px*@m;
	}
	
	.ReviewAlbum_tab li{
		font-family: PingFangSC-Regular;
		font-size: 18px*@m;
		color: #DCDCDC;
		letter-spacing: 0;
		line-height: 18px*@m;
		display: inline-block;
	}
	.ReviewAlbum_tab li:first-child{
		  margin-right: 39px*@m;
	}
	.ReviewAlbum_tab .tabActive{
		color: #0F2540;
	}
	
</style>
<script>
import { nativeBrowse } from '@/assets/js/bridge.js'; // 引入桥
import { isApp } from '@/assets/js/env'
import appMixins from '@/assets/js/appMixins.js'

import PictureBar from '@/components/picture-bar.vue'; //图片条
export default {
	mixins:[appMixins], 
	data () {
    return {
      data:{
				imgs_url: [
					"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1526623210095&di=b6b1afd7c32941696221d0a968736748&imgtype=0&src=http%3A%2F%2Fp3.gexing.com%2FG1%2FM00%2F39%2F56%2FrBACFFOJZ0mDlDkZAAAcODzXYPQ517.jpg",
					"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1526623210095&di=b6b1afd7c32941696221d0a968736748&imgtype=0&src=http%3A%2F%2Fp3.gexing.com%2FG1%2FM00%2F39%2F56%2FrBACFFOJZ0mDlDkZAAAcODzXYPQ517.jpg",
					"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1526623210095&di=b6b1afd7c32941696221d0a968736748&imgtype=0&src=http%3A%2F%2Fp3.gexing.com%2FG1%2FM00%2F39%2F56%2FrBACFFOJZ0mDlDkZAAAcODzXYPQ517.jpg"
				]
			}
    }
  },
  components:{
      PictureBar
  },
	methods:{
		getImgIndex(_i){
			nativeBrowse(this.data.imgs_url[_i])
			if(!isApp){}
		}
	},
  mounted(){

  }
}
</script>





